<template>
	<div id="poster-diy">
		<div class="content">
			<div class="img-wrap">
				<van-loading type="spinner" v-if="showPreLoading" />
				<img class="img-100" :src="url" alt="" v-else>
			</div>
			<div class="tip">
				<img src="static/img/postershare.png" alt="">
				<span class="font-16 c-white">长按上图保存图片，或发送给好友</span>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import {getData} from "@/controller/funcCommon"
	import cloudApp from '@/config/cloudapp.js'
	export default {
		data() {
			return {
				id: 0,
				type: 0,
				query: {},
				url: '',
				showPreLoading: true
			}
		},
		methods: {
			onLoad() {
				var that = this;
				getData({
					vue: that,
					url: 'poster/index',
					data: {
						id: that.id,
						type: that.type
					},
					success: function(res) {
						that.url = res.url;
						if(that.util.isGlala()) {
							cloudApp.longpress({
								url: that.url
							});
						}
					}
				});
			}
		},
		mounted() {
			this.query = this.util.extend(this.$route.query);
			this.id = this.query.id;
			this.type = this.query.type;
			this.onLoad();
		}
	}
</script>

<style>
	#poster-diy .content{
		background-color: #000;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
	}
	#poster-diy .img-wrap{
		margin: 20px 20px 15px;
		border-radius: 5px;
		overflow: hidden;
	}
	#poster-diy .img-wrap img{
		width: 100%;
		height: auto;
		pointer-events: auto;
	}
	#poster-diy .tip{
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		justify-content: center;
		font-size: 0px;
	}
	#poster-diy .tip img{
		width: 20px;
		margin-right: 5px;
	}
</style>


